    /* Positions the “FIBER FLOW” hero in the center or near center. 
    You can adjust top/left or use flex to place it exactly where you want. */
  .fiberflow-hero {
    position: absolute;
    top: 50%;
    left: 50%;
    width:100vw;
    transform: translate(-50%, -50%);
    pointer-events: none; /* So the text can be interactive if needed */
  }

  /* The parent container for the big heading */
  .fiberflow-hero .heading {
    position: relative;
    z-index: 2;
    /* This blend mode effect is optional */
    mix-blend-mode: difference;
    perspective: 1000px;
    backface-visibility: visible;
    transform: rotate(0.1deg);
  }

  /* A wrapper for the main text (if you want to pin or animate it, you can target .pin) */
  .fiberflow-hero .pin {
    position: relative;
  }

  /* The text itself */
  .fiberflow-hero h1 {
    position: relative;
    font-size: 12rem;       /* Adjust for your design */
    text-transform: uppercase;
    text-align: center;
    color: white;
    line-height: 1.1;
    margin: 0;
  }

  /* The clamp span holds the first word plus the arc SVG */
  .fiberflow-hero .clamp {
    position: relative;
    z-index: -1; /* Allows the arc to sit behind the text if you have blend modes */
  }

  /* The arc SVG inside .clamp */
  .fiberflow-hero .clamp svg {
    position: absolute;
    width: 112%;
    top: 50%;
    left: -6%;
    transform: translateY(-50%) rotate(2deg);
  }

  /* The second word or part of the heading */
  .fiberflow-hero .yt {
    z-index: 3; /* Renders above the arc if you’re using blend modes */
  }








  




