}

/* Gradient border effect */
.schedule-demo-btn::before {
  content: "";
  position: absolute;
  top: -4px;
  left: -4px;
  right: -4px;
  bottom: -4px;
  border-radius: 10px;
  padding: 3px;
  background: linear-gradient(90deg, rgba(131,58,180,1) 0%, rgba(253,29,29,1) 50%, rgba(252,176,69,1) 100%);
  -webkit-mask: linear-gradient(white, white) content-box, linear-gradient(white, white);
  mask: linear-gradient(white, white) content-box, linear-gradient(white, white);
  -webkit-mask-composite: xor;
  mask-composite: exclude;
  pointer-events: none;
  opacity: 0; /* Hidden initially */
  transition: opacity 0.3s ease-in-out;
}

/* Animated white background sliding from left to right */
.schedule-demo-btn::after {
  content: "";
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: white;
  transform: skewX(-20deg); /* Adds slight tilt effect */
  transition: left 0.5s ease-in-out;
}

/* Hover Effect */
.schedule-demo-btn:hover {
  color: black;
}

/* Ensure text is above sliding background */
.schedule-demo-btn span {
  position: relative;
  z-index: 3;
  display: inline-block;
  width: 100%;
  transition: color 0.3s ease-in-out;
}

/* Make gradient border visible */
.schedule-demo-btn:hover::before {
  opacity: 1;
}

/* Slide in white background */
.schedule-demo-btn:hover::after {
  left: 0;
}




